.wrapper {
    height: 100vh;
    width: 100%;
    // padding-top: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-left: 20px solid #7cb3f5;


    .aboutContainer {
        width: 50%;

        h2 {
            color: black;
            // -webkit-text-stroke: 0.1px black;
            font-size: 50px;
            opacity: 0.8;
            transition: opacity 1s ease-out;
            letter-spacing: 0.3rem;
            transform: scale(1.5, 1.5);
            -webkit-transform: scale(1.5, 1.5);
        }

        p {
            font-family: 'IBM Plex Mono', monospace !important;
            color: black;
            font-weight: bolder;
            font-size: 2vw;
            opacity: 0.8;
            padding: 20px;
        }
    }
}

@media (max-width:960px) {
    .wrapper {
        .aboutContainer {
            width: 80%;

            p {
                font-size: 15px;
            }
        }
    }
}